import React from 'react';
import './prices.css';

const Prices = () => {

  const navContacts = [{ text: "", href: "#contacts_en" }];

  const scrollToContacts = (href) => {
    const element = document.getElementById(href.substring(1));
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  const renderTrainingBlock = ({ name, description, price }) => {
    return (
      <div className="block" id='price_en'>
        <div className="training_name">{name}</div>
        <div className='description_button_block'>
          <div className="training_description">{description}</div>
          <div className="block_button">
            <div className="price_size">{price}</div>
            <div className="bob">
              {navContacts.map((item, index) => (
                <div key={index} className="nav-item">
                  <button className="bottom-right-button" onClick={() => scrollToContacts(item.href)}>
                    {item.text}
                  </button>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  };

  const trainingData = [
    {
      name: ' ',
      description: '          .       ,          .  - ,     .     - !',
      price: (
        <>
          60 min - 60 CHF
          <br />
          90 min - 80 CHF
        </>
      ),
    },
    {
      name: ' ',
      description: ', ,    .        .              .   -       .  - ,     .',
      price: (
        <>
          60 min - 110 CHF
          <br />
          90 min - 140 CHF
        </>
      ),
    },
    {
      name: ' ',
      description: '       ?       .          ,       . 2  + 2  (   + 50 CHF)',
      price: (
        <>
          60 min - 150 CHF
          <br />
          90 min - 200 CHF
        </>
      ),
    },
    {
      name: '  - Online',
      description: '      ,           .   ,        .         30 ,         .',
      price: (
        <>
          300 CHF  
          <br />
        </>
      ),
    },
    {
      name: '',
      description: '        .          ,   .       -.             .  -   . (  )',
      price: (
        <>
           150 CHF
          <br />
        </>
      ),
    },
    {
      name: '  5  10 ',
      description: '  ,      .       -     .        .  - ,     .',
      price: (
        <>
          5  - 450 CHF
          <br />
          10  - 850 CHF
        </>
      ),
    },
  ];

  return (
    <div className="container_price">
      {trainingData.map((data, index) => (
        <div key={index}>
          {renderTrainingBlock(data)}
        </div>
      ))}
    </div>
  );
}

export default Prices